import Form from "../common/form";
import carService from "./carService";
import Joi from "joi";

class EditCar extends Form {
  state = {
    form: {
      carNumber: "",
      manufacturer: "",
      model: "",
      manYear: "",
    },
  };

  schema = {
    _id: Joi.string(),
    manYear: Joi.number().min(1950).max(9999).messages({
      "number.base": `שדה שנת ייצור חייב להכיל מספרים בלבד`,
      "string.empty": `שנת ייצור הינה שדה חובה`,
      "number.min": `שנת ייצור מוגדרת החל משנת 1950`,
      "number.max": `לא ניתן להגדיר שנת ייצור כשנה עתידית`,
      "any.required": `שנת ייצור הינה שדה חובה`,
    }),
  };

  async doSubmit() {
    const { form } = this.state;
    await carService.editCar(form);

    this.props.history.replace("/my-cars");
  }

  mapToViewModel({ _id, carNumber, manufacturer, model, manYear }) {
    return {
      _id,
      carNumber,
      manufacturer,
      model,
      manYear,
    };
  }

  async componentDidMount() {
    const carId = this.props.match.params.id;
    const { data } = await carService.getCar(carId);
    this.setState({ form: this.mapToViewModel(data) });
  }

  handleCancel = () => {
    this.props.history.push("/my-cars");
  };

  render() {
    return (
      <div className="container d-flex justify-content-center">
        <div className="card-container">
          <div className="row">
            <div className="col-md-4 mx-auto text-center pb-5">
              <h3>ערוך רכב מספר </h3>
              <h3>{this.state.form.carNumber}</h3>
            </div>
          </div>

          <div className="row">
            <div className="col-md-4 mx-auto ">
              <h5 className="card-title border-top pt-3 ">
                <b>יצרן: </b> {this.state.form.manufacturer}
              </h5>
              <h5 className="card-title">
                <b>מודל: </b>
                {this.state.form.model}
              </h5>
            </div>
          </div>

          <form
            onSubmit={this.handleSubmit}
            noValidate="novalidate"
            autoComplete="off"
          >
            {
              <div className="row mt-5">
                {this.renderInput("manYear", "שנת ייצור")}

                <div className="row">
                  <div className="col-md-4 mx-auto text-center">
                    <button
                      onClick={this.renderButton}
                      className="btn btn-primary mt-5 me-5 "
                    >
                      שמור
                    </button>
                    <button
                      onClick={this.handleCancel}
                      className="btn btn-secondary mt-5 me-5 "
                    >
                      בטל
                    </button>
                  </div>
                </div>
              </div>
            }
          </form>
        </div>
      </div>
    );
  }
}

export default EditCar;
